const getRandomStr = function (count) {
  var str = '';
  for (var i = 0; i < count; i++) {
    str += Math.floor(Math.random() * 10);
  }
  return str;
};

const playerData = function () {
  const userId = getRandomStr(5)
  var that = {
    userData: JSON.parse(cc.sys.localStorage.getItem('userData')) || {
      userId, // 用户id
      userName: `guest_${userId}`, // 用户名称，guest_ 开头
      roomId: '',// 游戏房间id
      rootList: [
        {seatindex: 1, "accountid":"2117836", userName: `guest_${getRandomStr(5)}`,"avatarUrl":"avatar_2","goldcount": getRandomStr(4)},
        {seatindex: 2, "accountid":"2117837", userName: `guest_${getRandomStr(5)}`,"avatarUrl":"avatar_3","goldcount": getRandomStr(4)}
      ]
    },
    userId: cc.sys.localStorage.getItem('userId') || '', // 用户id
    userName: cc.sys.localStorage.getItem('userName') || '', // 用户名称，guest_ 开头
    roomId: cc.sys.localStorage.getItem('roomId') || '',// 游戏房间id
  }
  //that.uniqueID = "200000";
  //that.uniqueID = "1328014"
  that.uniqueID = 1 + getRandomStr(6)
  that.accountID = "2" + getRandomStr(6)
  var str = "avatar_" + (Math.floor(Math.random() * 3) + 1)
  that.avatarUrl = str   //随机一个头像
  that.gobal_count = cc.sys.localStorage.getItem('user_count')
  that.master_accountid = 0
  
  cc.sys.localStorage.setItem('userData', JSON.stringify(that))
  return that;
}

export default playerData
